import header from '@/components/header'
import image2 from '@/assets/index/permit_2.png'
import image4 from '@/assets/index/permit_4.png'
export default {
  data () {
    return {
      headtitle: '审批',
      image2: image2,
      image4: image4,
      showWindow: true, // 是否显示窗口审批
      showCheck: true, // 是否显示审批局审批
      jobType: ''
    }
  },
  created () {
    this.jobType = this.$store.getters.getUser.data.jobType
    if (this.jobType === '03') {
      this.showWindow = true
      this.showCheck = false
    } else if (this.jobType === '04' || this.jobType === '06' || this.jobType === '08') {
      this.showWindow = false
      this.showCheck = true
    } else if (this.jobType === '05' || this.jobType === '07') {
      this.showWindow = true
      this.showCheck = true
    }
  },
  components: {
    'v-header': header
  },

  methods: {
    // 跳转到审批列表页面
    goToPage (type) {
      this.$store.commit('setMenuType', { data: type })
      this.$router.push({ name: 'checkList' })
    }
  }
}




